/* eslint-disable no-useless-escape */
import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Cookies from "js-cookie";

import {
    Heading,
    TextInput,
    Box,
    Button,
    Card,
    Text,
    CardBody,
    CardFooter,
} from "grommet";
import { Link } from "@reach/router";

const Container = styled.div`
    min-height: 100vh;
    min-width: 500px;
    text-align: center;
`;

function BotonCustom({ link = "", label = "", ...props }) {
    return (
        <Button
            as={Link}
            to={link}
            label={label}
            size="large"
            onClick={() => {
                navigator.clipboard.writeText(
                    window.location.origin + "/" + link,
                );
            }}
            {...props}
        />
    );
}

export default function Main() {
    let cookieValue = Cookies.get("username");

    return (
        <Container>
            <Box pad="xlarge" align="center" gap="medium">
                <Heading margin="none">Hola {cookieValue}👋🏼</Heading>
                <BotonCustom
                    link="text"
                    label="TEXTO FLOTANTE (CLICK TO COPY)"
                />
                <BotonCustom link="confetti" label="CONFETTI" />
                <BotonCustom link="addvideos" label="ALERTAS VIDEO" />
            </Box>
        </Container>
    );
}
